import {EventEmitter, Injectable} from '@angular/core';
import {UserApi} from '../swagger/api/UserApi';
import {News} from '../_models/news';
import {GlobalService} from "./global.service";

@Injectable()
export class NewsService {

  NEWS: News[] = [];
  showNews = true;
  public static showApps = true;
  currNews: EventEmitter<boolean> = new EventEmitter();

  constructor(private service: UserApi) {
  }

  getNews() {
    return this.service.userNewsGet();
  }

  getNewsList(data) {
    if (this.NEWS.length > 0) {
      this.NEWS = [];
    }
    if (data) {
      for (let i = 0; i < data.length; i++) {
        this.NEWS.push(new News(data[i].id, i + 1, data[i].header, data[i].body, data[i].footer));
      }
      if (this.NEWS.length > 0) {
        this.currNews.emit(false);
      } else {
        // console.error("NO NEWS");
        this.currNews.emit(true);
      }
    }
    let mobileView = GlobalService.checkMobileView();
    if (mobileView && NewsService.showApps) {
      // console.error(mobileView);
      let obj = {
        title: '',
        body: '<span style="white-space: normal">Установите мобильное приложение</span><br>' +
        '<div style="display: inline">' +
        '<button class="app-btn" style="float: right; background-color: yellowgreen" >' +
        '<a class="app_href"' +
        'href="' + GlobalService.playstore + '">' +
        '   Скачать' +
        '   <img class="app-img" target="_blank" src=assets/images/icons/android.svg><br> ' +
        '</a></button>' +
        '<button class="app-btn" style="float: left; background-color: black">' +
        '<a class="app_href"  ' +
        'href="' + GlobalService.appstore + '">' +
        '   Скачать' +
        '   <img class="app-img" target="_blank" src=assets/images/icons/apple_icon.svg><br> ' +
        '</a></button>' +
        '</div>',
        closeable: true
      };
      GlobalService.showDialog(obj)
    }

    console.log(this.NEWS);
    return this.NEWS;
    // return [];
  }
}



