import {EventEmitter, Injectable} from '@angular/core';
import {ArticleApi} from '../swagger/api/ArticleApi';
import {Category} from '../_models/category';
import {GlobalService} from "./global.service";


@Injectable()
export class CategoryService {

  CATEGORIES: Array<Category> = [new Category(0, [{
    cult: '',
    value: ''
  }], 0)];
  CurrentCATEGORY = new Category(0, [{
    cult: '',
    value: ''
  }], 0);
  currCat: EventEmitter<number> = new EventEmitter();

  constructor(private service: ArticleApi,
              private global: GlobalService) {
  }

  // clearCategory() {
  //   this.CurrentCATEGORY = new Category(0, [{cult: "", value: ""}], 0);
  //   this.currCat.emit(this.CurrentCATEGORY.cId);
  // }

  setCategory(category) {
    this.CurrentCATEGORY = category;
    this.currCat.emit(this.CurrentCATEGORY.cId);
  }

  getCategories() {
    return this.service.articleCategoriesGet();
  }

  getCategoriesList(data) {
    if (this.CATEGORIES.length) {
      this.CATEGORIES = [];
    }
    for (let i = 0; i < data.length; i++) {
      this.CATEGORIES.push(new Category(data[i].id, data[i].name, data[i].order));
    }
    this.setCategoryByOrderId(1);
    return this.CATEGORIES;
  }


  setCategoryByOrderId(cOrder) {
    this.global.setActivity(0);
    let cat = this.CATEGORIES.filter(function (obj) {
      return obj.cOrder == cOrder;
    });
    console.log(cat[0]);
    this.setCategory(cat[0]);
  }
}



