export class Field {
  fId: number;
  fName: string[];
  fHint: string[];
  fMask: string;
  fRegexp: string;
  fOrder: number;

  constructor(fId: number, fName: string[], fHint: string[], fMask: string, fRegexp: string, fOrder: number) {
    this.fId = fId || 0;
    this.fName = fName || [];
    this.fHint = fHint || [];
    this.fMask = fMask || '';
    this.fRegexp = fRegexp || '';
    this.fOrder = fOrder || 0;
  }

  static toFields(data) {
    console.log('convert fields');
    let list: Field[] = [];
    if (data.length !== 0) {
      if (data[0].id) {
        console.log(data);
        for (let i = 0; i < data.length; i++) {
          list.push(
            new Field(data[i].id || 0, data[i].name || [], data[i].hint || [], data[i].mask || '', data[i].regexp || '', data[i].order ||
              0));
        }
      } else {
        console.log(data);
        list = data;
      }
      console.log(list);
      return list;
    } else {
      return [];
    }
  }

}



