import {Component, OnInit} from '@angular/core';
import {Lang} from '../translate/Lang';
import {OperationService} from '../_services/operation.service';
import {Product} from '../_models/product';
import {ProductService} from '../_services/product.service';
import {Field} from '../_models/field';

@Component({
  selector: 'app-fast-access',
  templateUrl: './fast-access.component.html',
  styleUrls: ['./fast-access.component.css']
})
export class FastAccessComponent
  implements OnInit {

  product: Product;
  fastList: Product[] = [];

  constructor(private service: OperationService, private products: ProductService) {
    this.service.currProduct.subscribe((value) => {
      this.product = value;
      console.log('CurrProduct');
      console.log(value);
    });
    this.products.currFastList.subscribe((val) => {
      this.fastList = val;
    });
  }

  ngOnInit() {
    console.warn("FAST INIT");
    this.fastList = [];
    if (localStorage.getItem('fastList')) {
      this.fastList = JSON.parse(localStorage.getItem('fastList'));
    }
    if (this.service.OPERATION.product.pId !== 0) {
      this.product = this.service.OPERATION.product;
    } else {
      this.product = new Product(0, 0, [], [], [], [], 0, '', [], 0, 0, '', 0);
    }
  }

  fastSelect(product) {
    console.log('PRODUCT SELECTED');
    console.log(product);
    product.pInputFields = Field.toFields(product.pInputFields);
    this.service.setProduct(product);
  }

  Lang(names) {
    return Lang.getLangValue(names);
  }

}



