import {EventEmitter, Injectable} from '@angular/core';
import {OperationApi} from '../swagger/api/OperationApi';
import {Product} from '../_models/product';
import {UserService} from './user.service';
import {GlobalService} from './global.service';

@Injectable()
export class OperationService {

  currStage: EventEmitter<number> = new EventEmitter();
  currProduct: EventEmitter<Product> = new EventEmitter();
  finalStatus: EventEmitter<number> = new EventEmitter();
  currTransId: EventEmitter<number> = new EventEmitter();

  public OPERATION;
  public redirect = false;

  error500 = {
    error: '500',
    errorText: [
      {cult: "uk", value: "Помилка сервера"},
      {cult: "ru", value: "Ошибка сервера"}
    ]
  };

  constructor(private service: OperationApi, private global: GlobalService, private user: UserService) {

    // this.payed.load();
    this.OPERATION = {
      opId: '',
      name: [],
      product: new Product(0, 0, [{
        cult: '',
        value: ''
      }], [{
        cult: '',
        value: ''
      }], [], [], 0, '', [], 0, 0, '', 0),
      fieldValues: [{
        id: 0,
        value: ''
      }],
      maskedValues: [],
      amount: 0,
      commission: 0,
      comment: '',
      status: {},
      opStage: 1,
      completed: 0
    };

    if (this.OPERATION.opStage != 3) {
      // console.error("CLEAR TIMER");
      // clearTimeout(this.timer)
    }
  }

  /**
   Set operation create stages :
   1 - input forms (show input-form.component),
   2 - verify operation (show check-begin.component),
   3 - execute and show status (show check-process.component)
   */
  setStage(stage) {
    if (this.global.checkOnline()) {
      this.OPERATION.opStage = stage;
      this.currStage.emit(this.OPERATION.opStage);
    }
  }

  /**
   Set operation status :
   1 - completed (green),
   2 - in progress (yellow),
   3 - failed (red)
   */
  setCompleted(status) {
    if (this.global.checkOnline()) {
      this.OPERATION.completed = status;
      this.finalStatus.emit(status);
    }
  }

  /**
   Method for selection of current product
   */
  setProduct(product) {
    console.warn("SET PRODUCT");
    this.OPERATION.product = product;
    this.OPERATION.fieldValues = [];
    for (let i = 0; i < product.pInputFields.length; i++) {
      this.OPERATION.fieldValues.push(
        {
          id: product.pInputFields[i].fId,
          value: ''
        }
      )
    }
    this.currProduct.emit(product);
  }

  /**
   Method set necessary data to defaults for next work
   */
  clearOperation() {
    console.log('CLEAR OPERATION');
    this.OPERATION.name = [];
    this.setProduct(new Product(0, 0, [{
      cult: '',
      value: ''
    }], [{
      cult: '',
      value: ''
    }], [], [], 0, '', [], 0, 0, '', 0));
    this.OPERATION.fieldValues = [{
      id: 0,
      value: ''
    }];
    this.OPERATION.maskedValues = [];
    this.OPERATION.amount = 0;
    this.OPERATION.commission = 0;
    this.OPERATION.comment = '';
    this.OPERATION.status = {};
    this.setStage(1);
    this.OPERATION.completed = 2; // 1 - success, 2 - in progress, 3 - error
  }

  // ========== OPERATION CREATION ========================================================================================
  /**
   Method for getting transaction Id and then run appropriate method for operation creation
   */
  createOperation() {
    if (this.global.checkOnline()) {
      console.log('CREATE OPERATION START');
      this.setStage(3);
      GlobalService.blockedNavigation = true;
      this.getTransId().subscribe(data => {
        this.currTransId.emit(data.transId);
        this.OPERATION.opId = data.transId.toString();
        const transId = data.transId;
        console.log('TRANS_ID = ' + transId);
        // VOUCHER
        if (this.OPERATION.product.pType === 1) {
          this.createVoucher(data.transId);
        }
        // REPLENISHMENT
        if (this.OPERATION.product.pType === 2) {
          this.createReplenishment(data.transId);
        }
      }, error => {
        // this.alert.onError(error);
        console.log('ERROR in getting TransId');
        console.log('ERROR_STATUS = ' + error.status);
        console.log('ERROR_BODY = ' + error._body);

        this.OPERATION.status = JSON.parse(error._body);
        console.log(error._body);
        this.setCompleted(3);
      }, () => {
      });
    }
  }

  // ---------- VOUCHER OPERATION -----------------------------------------------------------------------------------------
  createVoucher(transId) {
    const voucherOp = {
      article: this.OPERATION.product.pId,
      comment: this.OPERATION.comment || '',
      transId: transId
    };
    if (this.global.checkOnline()) {
      console.log('CREATE VOUCHER START');
      this.voucherCreate(voucherOp).subscribe(data => {
        console.log('VOUCHER GET STATUS_1  FOR ' + transId);
        console.log(data);
        this.getVoucherStatus(transId);
      }, error => {
        console.log('ERROR VOUCHER CREATE 1');
        console.log(error);
        const err = JSON.parse(error._body);
        switch (err.error) {
          case 8: {
            console.log('OPERATION EXISTS');
            this.getVoucherStatus(transId);
            break;
          }
          default: {
            break;
          }
        }
        this.voucherCreate(voucherOp).subscribe(data => {
          console.log('VOUCHER GET STATUS 2 TIME: ' + transId);
          console.log(data);
          this.getVoucherStatus(voucherOp.transId);
        }, error => {
          console.log('VOUCHER ERROR');
          console.log(error);

          this.OPERATION.status = JSON.parse(error._body);
          this.setCompleted(3);
          GlobalService.blockedNavigation = false;
        });
      });
    }
  }

  getVoucherStatus(tId) {
    if (this.global.checkOnline()) {
      console.log('GET STATUS FOR:' + tId);
      this.voucherGetStatus(tId).subscribe(data => {
        console.log('VOUCHER_STATUS 1:');
        console.log(data);
        if (data.serial) {
          this.OPERATION.status = data;
          this.setCompleted(1);
          GlobalService.blockedNavigation = false;
          this.user.refreshBalance();
          // console.error("TIMER START");
          // this.timer = setTimeout(() => {
          //   console.error("TIMER " + this.OPERATION.opId + " ENDS");
          //   this.clearOperation();
          // }, 15000);
        } else {
          this.voucherGetStatus(tId).subscribe(data => {
            console.log('VOUCHER_STATUS 2:');
            console.log(data);
            if (data.serial) {
              this.OPERATION.status = data;
              this.setCompleted(1);
              GlobalService.blockedNavigation = false;
              // this.timer = setTimeout(() => {
              //   console.error("TIMER " + this.OPERATION.opId + " ENDS");
              //   this.clearOperation();
              // }, 15000);
              // console.error("TIMER START");
              // setTimeout(this.timer);
            } else {
              this.voucherGetStatus(tId).subscribe(data => {
                console.log('VOUCHER_STATUS 3:');
                console.log(data);
                if (data.serial) {
                  this.OPERATION.status = data;
                  this.setCompleted(1);
                  GlobalService.blockedNavigation = false;
                } else {

                  this.OPERATION.status = data;
                  this.setCompleted(3);
                  GlobalService.blockedNavigation = false;
                }
              }, error => {
                this.OPERATION.status = JSON.parse(error._body);
                this.setCompleted(3);
                GlobalService.blockedNavigation = false;
              });
            }
          }, error => {
            this.OPERATION.status = JSON.parse(error._body);
            this.setCompleted(3);
            GlobalService.blockedNavigation = false;

          });
        }
      }, error => {
        this.OPERATION.status = JSON.parse(error._body);
        this.setCompleted(3);
        GlobalService.blockedNavigation = false;
      });
    }
  }

  // ---------- REPLENISHMENT OPERATION -----------------------------------------------------------------------------------
  createReplenishment(transId) {

    const replenishmentOp = {
      article: this.OPERATION.product.pId,
      fields: this.OPERATION.fieldValues,
      amount: Number(this.OPERATION.amount),
      comment: this.OPERATION.comment,
      transId: transId
    };
    if (this.global.checkOnline()) {
      console.log('CREATE REPLENISHMENT START');
      console.log('OPERATION:');
      console.log(replenishmentOp);
      this.setCompleted(2);
      GlobalService.blockedNavigation = true;
      this.replCreate(replenishmentOp).subscribe(data => {
        console.log('OPERATION GET STATUS 1 TIME: ' + transId);
        console.log(data);
        this.getReplenishmentStatus(transId);
      }, error => {
        if (error.status == 500) {
          this.setCompleted(3);
          GlobalService.blockedNavigation = false;
          this.OPERATION.status = this.error500;
        }
        if (error.status = 403) {
          let err = JSON.parse(error._body);
          switch (err.error) {
            case 8: {
              this.replGetStatus(transId);
              break;
            }
            case 923: {
              this.replCreate(replenishmentOp).subscribe(data => {
                console.log('OPERATION GET STATUS 2 TIME: ' + transId);
                console.log(data);
                this.getReplenishmentStatus(transId);
              }, error => {
                console.log('REPLENISHMENT ERROR');

                err = JSON.parse(error._body);
                this.OPERATION.status = err;
                this.setCompleted(3);
                GlobalService.blockedNavigation = false;
              });
              break;
            }
            default: {
              console.log("ERROR IN OPERATION CREATION");
              err = JSON.parse(error._body);
              this.OPERATION.status = err;
              this.setCompleted(3);
              GlobalService.blockedNavigation = false;
            }
          }
        }
      });

    }
  }

  getReplenishmentStatus(tId) {
    let err;
    if (this.global.checkOnline()) {
      console.log('REPLENISHMENT GET STATUS');
      this.replGetStatus(tId).subscribe(
        data => {
          console.log('REPLENISHMENT GET STATUS 1');
          console.log(data);
          switch (data.error) {
            // success
            case 0: {
              this.OPERATION.status = data;
              this.setCompleted(1);
              GlobalService.blockedNavigation = false;
              this.user.refreshBalance();

              break;
            }
            case 923: {
              console.warn('STATUS 923! Second attempt to request!');
              // REPL 2 ATTEMPT delay 5 sec
              setTimeout(() => {
                console.log('REPLENISHMENT GET STATUS 2');
                this.replGetStatus(tId).subscribe(data => {
                  console.log(data);
                  this.OPERATION.status = data;
                  switch (data.error) {
                    case 0: {
                      console.log('OPERATION SUCCESS');
                      this.setCompleted(1);
                      GlobalService.blockedNavigation = false;
                      this.user.refreshBalance();
                      // this.timer = setTimeout(() => {
                      //   console.error("TIMER " + this.OPERATION.opId + " ENDS");
                      //   this.clearOperation();
                      // }, 15000);
                      // console.error("TIMER START");
                      // setTimeout(this.timer);
                      break;
                    }
                    case 923: {
                      console.warn('STATUS 923! Third attempt to request!');
                      // REPL 3 ATTEMPT delay 10sec
                      setTimeout(() => {
                        console.log('REPLENISHMENT GET STATUS 3');
                        this.replGetStatus(tId).subscribe(data => {
                          switch (data.error) {
                            case 0: {
                              console.log(data);
                              this.setCompleted(1);
                              GlobalService.blockedNavigation = false;
                              this.user.refreshBalance();
                              // this.timer = setTimeout(() => {
                              //   console.error("TIMER " + this.OPERATION.opId + " ENDS");
                              //   this.clearOperation();
                              // }, 15000);
                              // console.error("TIMER START");
                              // setTimeout(this.timer);
                              break;
                            }
                            case 923: {
                              this.setCompleted(2);
                              GlobalService.blockedNavigation = false;
                              this.OPERATION.status = data;
                              this.user.refreshBalance();
                              break;
                            }
                            default: {
                              this.setCompleted(3);
                              GlobalService.blockedNavigation = false;
                              this.OPERATION.status = data;
                              this.user.refreshBalance();
                              break;
                            }
                          }
                        }, error => {
                          console.log('STATUS ERROR');
                          err = JSON.parse(error._body);
                          console.log(err);

                          this.OPERATION.status = err;
                          this.setCompleted(3);
                          GlobalService.blockedNavigation = false;
                        }, () => {
                          console.log('OPERATION COMPLETED 3');
                        });
                      }, 10000);
                      break;
                    }
                    default: {
                      this.OPERATION.status = data;
                      this.setCompleted(3);
                      GlobalService.blockedNavigation = false;
                    }
                  }
                }, error => {
                  // this.alert.onError(error);
                  err = JSON.parse(error._body);
                  console.log(err);
                  this.OPERATION.status = err;
                  this.setCompleted(3);
                  GlobalService.blockedNavigation = false;
                }, () => {
                  console.log('OPERATION COMPLETED 2');
                });
              }, 5000);
              break;
            }
            default: {
              this.OPERATION.status = data;
              this.setCompleted(3);
              GlobalService.blockedNavigation = false;
            }
          }
        }, error => {
          console.log("REPLENISHMENT ERROR 1");
          // this.alert.onError(error);
          if (error.status == 500) {
            this.OPERATION.status = this.error500;
          }
          err = JSON.parse(error._body);
          console.log(err);
          this.OPERATION.status = err;
          this.setCompleted(3);
          GlobalService.blockedNavigation = false;

        }, () => {
          console.log('OPERATION COMPLETED 1');
          //TODO for making tests
          // this.user.refreshBalance();
          // setTimeout(() => {
          //   this.clearOperation();
          // }, 2000);
          //------------------//
        });
    }
  }

  // ======================================================================================================================


  // ========= SWAGGER REQUESTING =========================================================================================

  getTransId() {
    if (this.global.checkOnline()) {
      return this.service.operationTransIdGet();
    }
  }

  replCreate(operation) {
    if (this.global.checkOnline()) {
      return this.service.operationReplenishmentPost(operation);
    }
  }

  replGetStatus(opId) {
    if (this.global.checkOnline()) {
      return this.service.operationReplenishmentOperationGet(opId);
    }
  }

  voucherCreate({article, comment, transId}) {// {article,amount,comment}
    if (this.global.checkOnline()) {
      return this.service.operationVoucherPost({
        article: article,
        comment: comment,
        transId: transId
      });
    }
  }

  voucherGetStatus(opId) {
    if (this.global.checkOnline()) {
      return this.service.operationVoucherOperationGet(opId);
    }
  }
}



