import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {UserService} from '../_services/user.service';
import {AuthenticationService} from '../_services/authentication.service';
import {Balance} from '../_models/balance';
import {GlobalService} from '../_services/global.service';
import {CategoryService} from "../_services/category.service";
import {ProductService} from "../_services/product.service";
import {OperationService} from "../_services/operation.service";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
})
export class HeaderComponent
  implements OnInit {

  public balance: Balance;
  showBalance = false;
  mobileView = false;
  windowWidth;
  public terminal = -1;
  public cashier = '';

  constructor(private service: UserService,
              private auth: AuthenticationService,
              private catService: CategoryService,
              private prodService: ProductService,
              private opService: OperationService,
              private cdr: ChangeDetectorRef) {
    this.service.currBalance.subscribe((value) => {
      this.balance = value;
    });
  }

  ngOnInit() {
    this.mobileView = GlobalService.checkMobileView();
    this.windowWidth = document.body.clientWidth;
    this.showBalance = false;
    this.balance = new Balance(0, 0, 0, null);
    const user = JSON.parse(localStorage.getItem('currentUser'));
    this.cashier = user.username;
    this.terminal = user.terminal;
    this.service.currBalance.subscribe((value) => {
      this.balance = value;
      this.showBalance = true
    });
    this.getBalance();
  }

  getBalance() {
    this.service.refreshBalance();
  }

  showMenu() {
    GlobalService.showMenu();
  }

  closeMenu() {
    GlobalService.closeMenu();
  }

  exit() {
    console.log('EXIT clicked!');
    // remove user from local storage to log user out
    if (!GlobalService.blockedNavigation)
      this.auth.logout();
  }

  showMore() {
    document.getElementById('mob_right_menu').classList.toggle('show-showmore');
  }

  showNews() {
    if (document.getElementById('modal-news'))
      document.getElementById('modal-news').classList.toggle('hide-news');
  }

  showHelp() {
    document.getElementById('help_block').classList.toggle('show-modal');
  }

  showBegin() {
    if (!GlobalService.blockedNavigation) {
      this.catService.setCategoryByOrderId(1);
      this.prodService.clearCurrent();
      this.opService.clearOperation();
    }
  }

}



