import {
  Component,
  Input,
  OnInit
} from '@angular/core';
import { OperationService } from '../_services/operation.service';

@Component({
  selector   : 'app-center',
  templateUrl: './center.component.html',
  styleUrls  : ['./center.component.css']
  // directives: [InputMobComponent, InputPayComponent, InputInetComponent, InputWebComponent]
})


export class CenterComponent
  implements OnInit {

  @Input('catId') catId: string;

  opStage;

  number = '';
  amount = 0.0;

  constructor(private service: OperationService) {
    this.service.currStage.subscribe((value) => {
      this.opStage = value;
    });
  }

  ngOnInit() {
    this.opStage = 1;
  }

}



