import {EventEmitter, Injectable} from '@angular/core';
import {ArticleApi} from '../swagger/api/ArticleApi';
import {Product} from '../_models/product';
import {Cheque} from '../_models/cheque';
import {Commission} from '../_models/commission';
import {GlobalService} from './global.service';
import {AlertService} from './alert.service';
import {Operation} from '../_models/operation';
import {UserService} from './user.service';
import {Lang} from '../translate/Lang';
import {OperationService} from "./operation.service";
import {Field} from "../_models/field";
import {CategoryService} from "./category.service";

@Injectable()
export class ProductService {


  fastProducts = [3, 5, 7, 16, 17];
  currProductList: EventEmitter<Product[]> = new EventEmitter();
  PRODUCTS: Product[] = [];
  fastAccess: Product[] = [];
  currFastList: EventEmitter<Product[]> = new EventEmitter();
  CHEQUES: Cheque[] = [];
  CurrentPRODUCT: Product = new Product(0, 0, [{
    cult: '',
    value: ''
  }], [{
    cult: '',
    value: ''
  }], [], [], 0, '', [], 0, 0, '', 0);
  ProdsByCat = [];

  constructor(private service: ArticleApi,
              private userServ: UserService,
              private alert: AlertService,
              private opService: OperationService,
              private catService: CategoryService,
              private global: GlobalService) {
  }

  selectFirstProduct() {
    console.log('PRODUCT SELECTED');
    let product = this.PRODUCTS.reduce(function (l, e) {
      return e.pOrder < l.pOrder ? e : l;
    });
    console.log(product);
    this.opService.clearOperation();
    product.pInputFields = Field.toFields(product.pInputFields);
    this.opService.setProduct(product);
    // this.service.clearSearchList();
    setTimeout(() => {
      const field = document.getElementById('fast-access');
      if (GlobalService.checkMobileView()) {
        field.scrollIntoView();
      }
    }, 300);
  }


  /**
   * Convert server responce to array of Commission obj
   * @param obj
   * @returns {Array}
   */
  static toCommission(obj) {
    const comm = [];
    for (let i = 0; i < obj.length; i++) {
      comm.push(new Commission(obj[i].minAmount, obj[i].percent || 0, obj[i].value || 0));
    }
    return comm;
  }

  getProducts(catId) {// getByCategory/
    if (this.global.checkOnline()) {
      return this.service.articleByCategoryCategoryGet(catId);
    }
  }

  /**
   * Set current product to default
   */
  clearCurrent() {
    this.CurrentPRODUCT = new Product(0, 0, [{
      cult: '',
      value: ''
    }], [{
      cult: '',
      value: ''
    }], [], [], 0, '', [], 0, 0, '', 0);
  }

  /**
   * Convert responce from server for category products to array of Product
   * @param data
   * @returns {Product[]}
   */
  getProductsList(data) {
    if (this.global.checkOnline()) {
      const fieldList = [];
      if (this.PRODUCTS.length) {
        this.PRODUCTS = [];
      }
      for (let i = 0; i < data.length; i++) {
        this.PRODUCTS.push(
          new Product(data[i].id, data[i].type, data[i].name || [], data[i].hint || [], data[i].field || [], ProductService.toCommission(
            data[i].commission) || [], data[i].nominal, data[i].cheque, data[i].codes, data[i].minAmount, data[i].maxAmount, data[i].icon,
            data[i].order,));
        fieldList.push({
          product: data[i].id,
          field: data[i].field || []
        });
      }
      this.PRODUCTS = this.PRODUCTS.sort(function (a, b) {
        return (a.pOrder > b.pOrder) ? 1 : ((b.pOrder > a.pOrder) ? -1 : 0);
      });
      console.warn(this.PRODUCTS);
      console.warn(fieldList);
      if (this.catService.CurrentCATEGORY.cOrder != 1) {
        this.selectFirstProduct();
      }
    }
    return this.PRODUCTS;
  }

  /**
   * Create array of products accessible from fast-access panel
   */
  formFastAccessList() {
    this.fastAccess = [];
    const products = this.PRODUCTS;
    for (let i = 0; i < products.length; i++) {
      if (this.fastProducts.includes(products[i].pId)) {
        this.fastAccess.push(products[i]);
      }
    }
    console.log('FAST PRODUCTS');
    console.log(this.fastAccess);
    localStorage.setItem('fastList', JSON.stringify(this.fastAccess));
    this.currFastList.emit(this.fastAccess);
  }


  getCheques() {
    if (this.global.checkOnline()) {
      return this.service.articleChequesGet();
    }
  }

  /**
   * Convert server responce to array of Cheque obj
   */
  getChequesList() {
    this.getCheques().subscribe(data => {
      if (this.CHEQUES.length) {
        this.CHEQUES = [];
      }
      for (let i = 0; i < data.length; i++) {
        this.CHEQUES.push(new Cheque(data[i].name, data[i].text));
      }
      localStorage.cheques = JSON.stringify(this.CHEQUES);
    }, error => {
      this.alert.onError(error);
    });
    console.log(this.CHEQUES);
  }

  /**
   * Getting HTML template of cheque from cheques array by it`s name
   * @param name - name of template
   * @returns {string}
   * @constructor
   */
  ChequeText(name) {
    for (let i = 0; i < this.CHEQUES.length; i++) {
      if (name === this.CHEQUES[i].cName) {
        return this.CHEQUES[i].cText;
      }
    }
  }


  /**
   * Method for printing cheque
   * @param {Operation} Operation - obj with necessary values
   * @param {string} chequeTemplate - HTML template of cheque
   */
  printCheque(Operation: Operation, chequeTemplate: string): void {
    let popupWin, cheque;

    cheque = chequeTemplate.replace(/\n/g, '<br>');
    console.log(cheque);
    const terminal = this.userServ.USER.terminal.toString();
    cheque = cheque.replace('<TermID>', '' + terminal + '');
    const d = new Date(Operation.oTimestamp);
    const date = '' + d.getDate() + '.' + ('0' + (d.getMonth() + 1)).slice(-2) + '.' + d.getFullYear() + ' ' + d.getHours() + ':' +
      d.getMinutes() + ':' + d.getSeconds();
    cheque = cheque.replace('<DateSale>', '' + date + '');
    const name = Lang.getLangValue(Operation.oArticle);
    cheque = cheque.replace('<ProductName>', name + '');
    if (chequeTemplate.includes('<AmountTotal>')) {

      cheque = cheque.replace('<Fld1Value>', Operation.oFields[0].value);
      cheque = cheque.replace('<Fee>', Math.abs(Operation.oCommission) / 100);
      if (Operation.oCommission >= 0) {
        cheque = cheque.replace('<AmountTotal>', (Operation.oAmount / 100 + Operation.oCommission / 100));
      } else {
        cheque = cheque.replace('<AmountTotal>', Operation.oAmount);
      }
      cheque = cheque.replace('<OpState>', Operation.oStatus.error + ' - ' + Lang.getLangValue(Operation.oStatus.errorText));
      cheque = cheque.replace('<UNIQID>', Operation.oId);
    }

    if (chequeTemplate.includes('<CardSerial>')) {
      cheque = cheque.replace('<CardSerial>', Operation.oStatus.serial);
      cheque = cheque.replace('<CardSecret>', Operation.oStatus.secret);
      cheque = cheque.replace('<CardSecret\>', Operation.oStatus.secret);
    }
    if (Operation.oCommission >= 0) {
      cheque = cheque.replace('<Nominal>', Operation.oAmount / 100);
    } else {
      cheque = cheque.replace('<Nominal>', (Operation.oAmount + Operation.oCommission) / 100);
    }

    const content = '<html><head><title>Print cheque</title></head>' + '<body onload="window.print();window.close()">' + cheque +
      '</body></html>';

    console.log(content);
    popupWin = window.open('', '', 'left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0');
    // popupWin.document.open();
    popupWin.document.write(content);
    // popupWin.document.print();
    popupWin.document.close();
  }
}



