import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-keyboard',
  templateUrl: './keyboard.component.html',
  styleUrls: ['./keyboard.component.css']
})
export class KeyboardComponent
  implements OnInit {

  currKeyboard = 1;
  upperCase = false;
  @Output() onBtnClick = new EventEmitter<string>();


  constructor() {
  }

  ngOnInit() {

  }


  changeKeyboard(val) {
    this.currKeyboard = val;
    this.upperCase = false;
  }

  pressButton(val) {
    if (!this.upperCase) {
      this.onBtnClick.emit(val);
    } else {
      this.onBtnClick.emit(val.toUpperCase());
    }
  }


  changeToUpper() {
    this.upperCase = !this.upperCase;
    const letters = document.getElementsByClassName('keyboard-wrap letters')[0];
    if (this.upperCase)
      letters.classList.add('uppercased');
    else
      letters.classList.remove('uppercased');
  }

  removeSymbol() {
    this.onBtnClick.emit('bksp');
  }
}



