import {
  Pipe,
  PipeTransform
} from '@angular/core';

@Pipe({
  name: 'toRegex'
})
export class ToRegexPipe
  implements PipeTransform {

  transform(value: any): any {
    console.log(value);
    const test = [];

    {
      for (let i = 0; i < value.length; i++) {
        if (value[i].match(/[_*0-9]/g)) {
          test.push(/\d/);
        } else {
          test.push(value[i]);
        }
      }
    }
    console.log(test);
    return test;
  }

}



