///<reference path="../../translate/translate.pipe.ts"/>
import {Component, Inject, OnInit} from '@angular/core';
import {ReportsService} from '../../_services/reports.service';
import {AlertService} from '../../_services/alert.service';
import {Operation} from '../../_models/operation';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {ProductService} from '../../_services/product.service';
import {Lang} from '../../translate/Lang';
import {OperationService} from '../../_services/operation.service';
import {GlobalService} from "../../_services/global.service";

@Component({
  selector: 'app-operation-report',
  templateUrl: './operation-report.component.html',
  styleUrls: ['./operation-report.component.css'],
})
export class OperationReportComponent
  implements OnInit {

  loading = false;
  mobileView = GlobalService.checkMobileView();
  reportArray: Operation[] = [];
  sort_types = [{
    value: 'newer',
    desc: 'desc'
  },
    {
      value: 'older',
      desc: 'asc'
    }];
  sort_desc = this.sort_types[0].desc;
  lang = 'ru';
  sDate: Date = new Date;
  eDate: Date = new Date;
  count = 100;

  constructor(private report: ReportsService,
              private alert: AlertService,
              public dialog: MatDialog) {
  }

  ngOnInit() {
    this.lang = localStorage.lang;
    this.sDate.setHours(0);
    this.sDate.setMinutes(0);
    this.sDate.setSeconds(0);
    this.sDate.setMilliseconds(0);

    this.eDate.setHours(23);
    this.eDate.setMinutes(59);
    this.eDate.setSeconds(59);
    this.eDate.setMilliseconds(999);
  }

  sendRequest() {
    if (this.checkDates()) {
      this.reportArray = [];
      this.loading = true;
      console.log(this.sDate.toISOString());
      console.log(this.eDate.toISOString());
      console.log(this.sort_desc);
      console.log(this.count);
      this.report.getOperationHistory(this.count, this.sDate, this.eDate, 0, this.sort_desc).subscribe(data => {
        console.log('OPERATION HISTORY');
        console.log(data);
        this.loading = false;
        this.reportArray = ReportsService.toOpList(data);
        console.log(this.reportArray);
      }, error => {
        this.loading = false;
        this.alert.onError(error);
      });
    }
  }

  checkDates() {
    if (this.sDate.getTime() > this.eDate.getTime()) {
      document.getElementById('EndDate').style.borderBottomColor = 'red';
      document.getElementById('StartDate').style.borderBottomColor = 'red';
      document.getElementById('date-error').style.display = 'block';
      return false;
    } else {
      document.getElementById('EndDate').style.borderBottomColor = '#898989';
      document.getElementById('StartDate').style.borderBottomColor = '#898989';
      document.getElementById('date-error').style.display = 'none';
      return true;
    }
  }

  openDialog(operation: Operation) {
    let config = {};
    if (window.innerWidth < 721) {
      config = {
        height: '80vh',
        width: '80vw',
        data: operation
      };
    } else {
      config = {
        height: '550px',
        width: '400px',
        data: operation
      };
    }
    const dialogRef = this.dialog.open(MyDialog, config);
    dialogRef.componentInstance.Operation = operation;
  }

  setLang(name) {
    return Lang.getLangValue(name);
  }


}


@Component({
  selector: 'opDialog',
  templateUrl: 'dialog.html',
})
export class MyDialog {
  Operation: Operation = new Operation('', [{
    cult: '',
    value: ''
  }], 0, '', [{
    id: 0,
    value: ''
  }], 0, 0, '', {
    error: 0,
    errorText: [{
      cult: '',
      value: ''
    }]
  }, '', '');

  onBalance = 0;
  inAmount = 0;
  sumCommission = 0;
  loading = false;
  cheque = '';

  constructor(private opService: OperationService,
              public dialogRef: MatDialogRef<MyDialog>,
              private prodServ: ProductService,
              @Inject(MAT_DIALOG_DATA) public data: any) {
    this.calcValues();
  }

  ngOnInit() {
    this.calcValues();
    setTimeout(() => {
      const f = document.getElementById('dialog-header');
      f.scrollIntoView();
    }, 200);
  }

  public setL(names) {
    return Lang.getLangValue(names);
  }


  calcValues() {
    this.sumCommission = this.Operation.oCommission;
    // console.log(operation.commission);
    if (this.sumCommission > 0) {
      this.onBalance = this.Operation.oAmount / 100;
      this.sumCommission = Math.abs(this.sumCommission / 100);
      this.inAmount = this.Operation.oAmount / 100 + this.sumCommission;
    }
    if (this.sumCommission === 0) {
      this.onBalance = this.Operation.oAmount / 100;
      this.sumCommission = Math.abs(this.sumCommission / 100);
      this.inAmount = this.Operation.oAmount / 100;
    }
    if (this.sumCommission < 0) {
      this.sumCommission = Math.abs(this.sumCommission / 100);
      this.onBalance = this.Operation.oAmount / 100 - this.sumCommission;
      this.inAmount = this.Operation.oAmount / 100;
    }
  }


  getOpStatus(transId) {
    if (this.Operation.oType === 1) {
      this.loading = true;
      this.opService.voucherGetStatus(transId).subscribe(data => {
        console.log(data);
        this.Operation.oStatus = data;
        this.loading = false;
      }, error => {
        this.loading = false;
        this.Operation.oStatus = JSON.parse(error._body);
        // this.alert.onError(error);
      });
    }
    if (this.Operation.oType === 2) {
      console.log('NOT VOUCHER');
      this.loading = true;
      this.opService.replGetStatus(transId).subscribe(data => {
        console.log(data);
        this.Operation.oStatus = data;
        this.loading = false;
      }, error => {
        console.error(error);
        this.loading = false;
        if (error._body) {
          // console.error("TEST1");
          this.Operation.oStatus = JSON.parse(error._body);
        } else {
          // console.error("TEST2");
          this.Operation.oStatus = {
            error: 923,
            errorText: [
              {cult: 'ru', value: 'Транзакция в обработке.\n Попробуйте позже'},
              {cult: 'ua', value: 'Транзакція в обробці.\n Спробуйте пізніше'}]
          };
        }
        // this.alert.onError(error);
      });
    }
  }

  printCheque() {
    console.log("Print cheque");
    const cheque = this.prodServ.ChequeText(this.Operation.oCheque);
    this.prodServ.printCheque(this.Operation, cheque);
  }

}



