import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';

import {AuthenticationService} from '../_services/index';
import {Configuration} from '../swagger/configuration';
import {UserService} from '../_services/user.service';
import {GlobalService} from "../_services/global.service";

@Component({
  moduleId: module.id,
  templateUrl: 'login.component.html',
  styleUrls: ['./login.css']
})

export class LoginComponent
  implements OnInit {
  model: any = {};
  loading = false;
  incorrect = false;
  token = "";
  mobileView;
  windowWidth;
  private sub: any;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private configuration: Configuration,
              private authenticationService: AuthenticationService,
              private user: UserService) {
  }

  ngOnInit() {
    this.mobileView = GlobalService.checkMobileView();
    this.windowWidth = document.body.clientWidth;
    // reset login status
    // this.authenticationService.logout();
    console.log(JSON.parse(localStorage.getItem('currentUser')));
    // get return url from route parameters or default to '/'
    if (!localStorage.getItem('currentUser')) {
      if (GlobalService.redirectToMain) {
        this.authenticationService.logout();
      } else {
        // this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
      }
    } else {
      this.user.USER = JSON.parse(localStorage.getItem('currentUser'));
      this.configuration.apiKey = JSON.parse(localStorage.getItem('currentUser')).keyToken;
    }
  }

  login() {
    this.loading = true;
    this.incorrect = false;
    if (this.model.username !== '' && this.model.password !== '' && this.model.terminal !== '') {
      console.log('LOGIN ON');
      this.authenticationService.loginAuth(this.model.username, this.model.password, this.model.terminal).subscribe(data => {
        console.log(data);
        if (data.tokens.keyToken !== '') {
          try {
            localStorage.setItem('currentUser', JSON.stringify({
              username: this.model.username,
              terminal: this.model.terminal,
              keyToken: data.tokens.keyToken,
              refreshToken: data.tokens.refreshToken
            }));
            console.warn("login data saved to storage");
            this.user.USER = JSON.parse(localStorage.getItem('currentUser'));
          } catch (error) {
            alert('Для авторизации на сайте отключите режим инкогнито');
            this.loading = false;
          }
          // set token value for requests
          this.configuration.apiKey = data.tokens.keyToken;
        }
        this.loading = false;
        this.router.navigate(['/']);
        this.user.getGeo();
      }, error => {
        let info = JSON.parse(error._body);
        // console.error(info);
        if (info.error == 4)
          this.incorrect = true;
        // // this.alertService.error(error);
        this.loading = false;
      });
      setTimeout(() => {
        this.loading = false;
        // TODO timeout message init
      }, 30000);
    }
  }
}



