import {Component, OnInit} from '@angular/core';
import {Lang} from '../translate/Lang';
import {GlobalService} from '../_services/global.service';
import {DateAdapter, NativeDateAdapter} from '@angular/material';


@Component({
  selector: 'app-nav-menu',
  templateUrl: './nav-menu.component.html',
  styleUrls: ['./nav-menu.component.css']
})


export class NavMenuComponent
  implements OnInit {

  public supportedLanguages: any[];
  public showList = false;
  private cur_lang;

  constructor(private dateAdapter: DateAdapter<NativeDateAdapter>, private global: GlobalService) {
    GlobalService.showList.subscribe(
      (val) => {
        this.showList = val
      }
    )
  }

  ngOnInit() {
    this.setLang();
    this.showList = false;
    GlobalService.showList.subscribe(
      (val) => {
        this.showList = val
      }
    )
  }


  // ===========================================
  setLang() {
    if (localStorage.lang) {
      this.cur_lang = localStorage.lang;
    } else {
      this.cur_lang = Lang.def_lang;
    }
    this.supportedLanguages = [
      {
        display: 'Русский',
        value: 'ru'
      },
      {
        display: 'Українська',
        value: 'uk'
      }
    ];
    this.selectLang(this.cur_lang);
  }

  isCurrentLang(lang: string) {
    return lang === localStorage.lang;
  }

  selectLang(lang: string) {
    // set default;
    if (lang === 'ru') {
      this.dateAdapter.setLocale('ru-UA');
    }
    if (lang === 'uk') {
      this.dateAdapter.setLocale('uk-UA');
    }
    localStorage.lang = lang;
    this.cur_lang = lang;
    this.showList = false;
  }

  showLangList() {
    this.showList = !this.showList;
    setTimeout(function () {
      this.showList = false;
    }, 4000);
  }

  goToActivity(act) {
    if (!GlobalService.blockedNavigation) {
      this.global.setActivity(act);
      this.showList = false;
    }
  }
}



