import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Product} from '../_models/product';
import {Lang} from '../translate/Lang';
import {AlertService} from '../_services/alert.service';
import {ProductService} from '../_services/product.service';
import {OperationService} from '../_services/operation.service';
import {CategoryService} from '../_services/category.service';
import {Field} from '../_models/field';
import {GlobalService} from '../_services/global.service';


@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent
  implements OnInit {


  PRODUCTS: Product[];

  @Input('catId') catId: number;
  // @Input('catTag') catTag: string;


  @Output() emitEvent = new EventEmitter();
  currProduct;

  constructor(private service: ProductService,
              private catService: CategoryService,
              private opService: OperationService,
              private alert: AlertService) {
    this.opService.currProduct.subscribe((value) => {
      console.warn("PRODUCT CHANGED");
      this.currProduct = value;
    });
    this.catService.currCat.subscribe((value) => {
      this.catId = value;

    });
    this.service.currProductList.subscribe((val) => {
      this.PRODUCTS = val;
    });


  }

  ngOnInit() {
    this.opService.clearOperation();
    this.opService.currProduct.subscribe((value) => {
      this.currProduct = value;
    });
    this.catService.currCat.subscribe((value) => {
      this.catId = value;
    });
    this.service.currProductList.subscribe((val) => {
      this.PRODUCTS = val;
    });

    let currentCatId = 0;
    let prodByCat;
    if (sessionStorage.ProductsByCategory) {
      prodByCat = JSON.parse(sessionStorage.ProductsByCategory);
      for (let i = 0; i < prodByCat.length; i++) {
        if (prodByCat[i].category === this.catId) {
          currentCatId = i;
        }
      }
    }
    if (this.catId !== 0) {
      this.service.getProducts(this.catId).subscribe(data => {
        this.PRODUCTS = this.service.getProductsList(data);
        console.log('PRODUCTS FOR ' + this.catId.toString());
        console.log(this.PRODUCTS);
        this.service.ProdsByCat[currentCatId].products = this.PRODUCTS;
        this.service.formFastAccessList();
        console.warn("FAST FORMED");
        // if (this.catService.CurrentCATEGORY.cOrder !== 1) {
        //   console.warn("autoselect product");
        //   if (this.PRODUCTS.length > 0) {
        //     this.selectProduct(
        //       this.PRODUCTS.reduce(function (l, e) {
        //         return e.pOrder < l.pOrder ? e : l;
        //       }))
        //   }
        // }
      }, error => {
        // console.log("STATUS = " + error.status);
        this.alert.onError(error);
      });
    }
  }


  selectProduct(product) {
    if (!GlobalService.blockedNavigation) {
      console.log('PRODUCT SELECTED');
      console.log(product);
      this.opService.clearOperation();
      product.pInputFields = Field.toFields(product.pInputFields);
      this.opService.setProduct(product);
      // this.service.clearSearchList();
      setTimeout(() => {
        const field = document.getElementById('fast-access');
        if (GlobalService.checkMobileView()) {
          field.scrollIntoView();
        }
      }, 300);
    }
  }

  Lang(names) {
    return Lang.getLangValue(names);
  }


  // }
}



