import {Component, OnInit} from '@angular/core';
import {GlobalService} from '../../_services/global.service';
import {ReportsService} from '../../_services/reports.service';
import {Lang} from '../../translate/Lang';

@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.css',]
})
export class ReportsComponent
  implements OnInit {

  mobileView = GlobalService.checkMobileView();
  /**
   * Activities
   * 1 - sales
   * 2 - opHistory
   * 3 - collection
   * 4 - detailed sales
   * 5 - financial
   * 6 - shift
   * OPTIONS
   * (9) - my contacts (in future)
   * 7 - authHistory
   * 8 - changePassword
   * */
  currActivity = this.global.activity;
  reportContent = '';
  loading = false;

  actName = 'reports';
  buttonName = 'show';
  success_collect = false;
  sDate: Date = new Date;
  eDate: Date = new Date;

  date_fields = 0;
  showButton = true;
  repDone;

  constructor(private global: GlobalService, private report: ReportsService) {
    this.success_collect = false;
  }

  ngOnInit() {
    this.repDone = false;

    this.sDate.setHours(0);
    this.sDate.setMinutes(0);
    this.sDate.setSeconds(0);
    this.sDate.setMilliseconds(0);

    this.eDate.setHours(23);
    this.eDate.setMinutes(59);
    this.eDate.setSeconds(59);
    this.eDate.setMilliseconds(999);

    this.success_collect = false;
    this.reportContent = '';
    this.report.currReportContent.subscribe((val) => {
      this.reportContent = val;
      this.repDone = true;
      this.loading = false;
    });
    this.global.currActivity.subscribe((val) => {
      this.currActivity = val;
      this.setCurrReport(this.currActivity);
      console.log('CURR_ACTIVITY = ' + this.currActivity);
    });
    this.setCurrReport(this.currActivity);
    console.log('CURR_ACTIVITY = ' + this.currActivity);
  }


  clearContent() {
    this.repDone = false;
    this.reportContent = '';
  }

  setCurrReport(val) {
    switch (val) {
      case 1: {// Продажи
        this.clearContent();
        this.buttonName = 'show';
        this.actName = 'sales';
        this.date_fields = 2;
        this.showButton = true;
        break;
      }
      case 3: {// Инкассация
        this.clearContent();
        this.actName = 'collection';
        this.buttonName = 'create_collection';
        this.date_fields = 0;
        this.report.getShift();
        this.showButton = true;
        break;
      }
      case 4: {// Продажи детальные
        this.clearContent();
        this.buttonName = 'show';
        this.actName = 'details';
        this.date_fields = 2;
        this.showButton = true;
        break;
      }
      case 5: {// Финансовый
        this.clearContent();
        this.buttonName = 'show';
        this.actName = 'financial';
        this.date_fields = 1;
        this.showButton = true;
        break;
      }
      case 6: {// Отчет за смену
        this.clearContent();
        this.buttonName = 'show';
        this.actName = 'duty_report';
        this.date_fields = 0;
        this.showButton = false;
        this.report.getShift();
        break;
      }
      case 7: {// История входов
        this.clearContent();
        this.buttonName = 'show';
        this.actName = 'access_control';
        this.date_fields = 0;
        this.showButton = false;
        break;
      }
      default: {
        this.clearContent();
        this.actName = '';
        this.date_fields = 0;
        this.showButton = false;
        break;
      }

    }
  }

  setLang(name) {
    return Lang.getLangValue(name);
  }

  checkDates() {
    console.warn(this.sDate);
    console.warn(this.eDate);
    if (this.date_fields === 2) {
      if (this.sDate.getTime() > this.eDate.getTime()) {
        document.getElementById('EndDate').style.borderBottomColor = 'red';
        document.getElementById('StartDate').style.borderBottomColor = 'red';
        document.getElementById('date-error').style.display = 'block';
        return false;
      } else {
        document.getElementById('EndDate').style.borderBottomColor = '#898989';
        document.getElementById('StartDate').style.borderBottomColor = '#898989';
        document.getElementById('date-error').style.display = 'none';
        return true;
      }
    }
    if (this.date_fields <= 1) {
      return true;
    }
  }

  printReport() {
    let report = {
      content: this.reportContent,
      sDate: [3, 5, 6].includes(this.currActivity) ? null : this.sDate,
      eDate: [3, 5, 6].includes(this.currActivity) ? null : this.eDate,
      name: document.getElementById('actName').innerText
    };
    ReportsService.printReport(report);
  }

  completeAction() {
    if (this.checkDates()) {
      switch (this.currActivity) {
        case 1: {
          this.report.getSales(this.sDate, this.eDate);
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
          }, 40000);
          break;
        }
        case 3: {
          console.warn('COLLECTION');
          this.report.createCollection();
          this.loading = true;
          this.showButton = false;
          setTimeout(() => {
            this.loading = false;
          }, 40000);
          break;
        }
        case 4: {
          this.report.getSalesDetailed(this.sDate, this.eDate);
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
          }, 40000);
          break;
        }
        case 5: {
          // console.log(this.sDate.toISOString());
          this.report.getFinancial(this.sDate);
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
          }, 40000);
          break;
        }
        default: {
          this.loading = false;
        }
      }
    }
  }
}



