export class News {
  nId: number;
  nOrder: number;
  nTitle: string;
  nBody: string;
  nFooter: string;

  constructor(nId: number, nOrder: number, nTitle: string, nBody: string, nFooter: string) {
    this.nId = nId;
    this.nOrder = nOrder;
    this.nTitle = nTitle;
    this.nBody = nBody;
    this.nFooter = nFooter;
  }
}



