import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {AuthApi} from '../swagger/api/AuthApi';
import {UserApi} from '../swagger/api/UserApi';
import {GlobalService} from "./global.service";

@Injectable()
export class AuthenticationService {
  constructor(private service: AuthApi, private user: UserApi) {
  }

  loginAuth(username: string, pass: string, term: string) {
    console.log("run LoginAuth");
    return this.service.authLoginPost({
      login: username,
      password: pass,
      terminal: term
    });
  }

  loginByToken(token: string) {
    console.log("run loginByToken");
    return this.service.authOtpOtpUuidGet(token);
  }

  logout() {
    // remove user from local storage to log user out
    if (GlobalService.redirectToMain) {
      window.location.href = GlobalService.redirectPath;
    } else {
      window.location.href = '/';
    }
    localStorage.removeItem('currentUser');
    // navigator.geolocation.clearWatch();
    this.service.authLogoutGet();
  }

  changePass(passwords) {
    return this.user.userPasswordPut(passwords);
  }


}



