///<reference path="../swagger/model/Auth.ts"/>
import {EventEmitter, Injectable} from '@angular/core';
import {UserApi} from '../swagger/api/UserApi';
import {Balance} from '../_models/balance';
import {AlertService} from './alert.service';

@Injectable()
export class UserService {
  BALANCE: Balance = new Balance(0, 0, 0, null);
  currBalance: EventEmitter<Balance> = new EventEmitter();
  USER = JSON.parse(localStorage.getItem('currentUser'));

  constructor(public service: UserApi, private alert: AlertService) {
  }


  refreshBalance() {
    console.log('GET BALANCE');
    this.getBalance().subscribe(data => {
      this.BALANCE = new Balance(data.personal, data.network, data.overdraft, new Date(data.datetime));
      console.log('USER BALANCE:' + data.personal);
      this.currBalance.emit(this.BALANCE);
    }, error => {
      this.alert.onError(error);
    });
  }

  getBalance() {
    return this.service.userBalanceGet();
  }

  getSupport() {
    return this.service.userSupport2Get();
  }


  // ==========================================================================================================


  getGeo() {
    console.log('GETTING GEO');
    const options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    };
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(this.workWithGeo.bind(this), null, options);
    }
  }

  workWithGeo(position) {
    console.log('GEOLOCATION');
    const positionInfo = {
      latitude: position.coords.latitude,
      longitude: position.coords.longitude,
      timestamp: new Date(position.timestamp)
    };
    console.log(positionInfo);
    // alert("My position: " + positionInfo.longitude + " || " + positionInfo.latitude);
    this.service.userGeolocationPost(positionInfo).subscribe(data => {
      console.log('GEO-RESPONCE');
      console.log(data);
    }, error => {
      console.log('GEO-ERROR');
      this.alert.onError(error);
    });
  }

  // ==========================================================================================================

}



