///<reference path='input-form/input-form.component.ts'/>
import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BaseRequestOptions, HttpModule} from '@angular/http';
import {AppComponent} from './app.component';
import {HeaderComponent} from './header/header.component';
import {KeyboardComponent} from './keyboard/keyboard.component';
import {HelpComponent} from './help/help.component';
import {NavMenuComponent} from './nav-menu/nav-menu.component';
import {NewsComponent} from './news/news.component';

import {CategoriesComponent} from './categories/categories.component';
import {FastAccessComponent} from './fast-access/fast-access.component';
import {ProductListComponent} from './product-list/product-list.component';

import {CenterComponent} from './center/center.component';
import {CheckBeginComponent} from './check-forms/check-begin/check-begin.component';
import {CheckProcessComponent} from './check-forms/check-process/check-process.component';
import {InputFormComponent} from './input-form/input-form.component';
import {TranslatePipe} from './translate/translate.pipe';
import {TRANSLATION_PROVIDERS} from './translate/translations';
import {routing} from './app.routing';
import {AuthGuard} from './_guards/auth.guard';
import {AlertService} from './_services/alert.service';
import {AuthenticationService} from './_services/authentication.service';
import {UserService} from './_services/user.service';
import {GlobalDialog, HomeComponent} from './home/home.component';
import {LoginComponent} from './login/login.component';
import {APP_BASE_HREF} from '@angular/common';
import {AlertComponent} from './_directives/alert.component';
import {UserApi} from './swagger/api/UserApi';
import {ArticleApi} from './swagger/api/ArticleApi';
import {OperationApi} from './swagger/api/OperationApi';
import {Configuration} from './swagger/configuration';
import {CategoryService} from './_services/category.service';
import {NewsService} from './_services/news.service';
import {ProductService} from './_services/product.service';
import {OperationService} from './_services/operation.service';
import {AuthApi} from './swagger/api/AuthApi';
import {ReportApi} from './swagger/api/ReportApi';
import {NgPipesModule} from 'ngx-pipes';
import {SafeHtmlPipe} from './_pipes/safeHTML';
import {GlobalService} from './_services/global.service';
import {MyCurrencyPipe} from './_pipes/myCurrency';

import {
  MatDatepickerModule,
  MatDialogModule,
  MatInputModule,
  MatNativeDateModule,
  MatSelectModule,
  MatTableModule
} from '@angular/material';
import {NoopAnimationsModule} from '@angular/platform-browser/animations';
import {ReportsService} from './_services/reports.service';
import {MyDialog, OperationReportComponent} from 'app/report_components/operation-report/operation-report.component';
import {ReportsComponent} from './report_components/reports/reports.component';
import {TextMaskModule} from 'angular2-text-mask';
import {ChangePassComponent} from './change-pass/change-pass.component';
import {ToRegexPipe} from './_pipes/to-regex.pipe';
import {AuthHistoryComponent} from './report_components/auth-history/auth-history.component';
import {StatusComponent} from './status/status.component';
import 'hammerjs';
import 'hammer-timejs';
import {CurrencyMaskModule} from 'ng2-currency-mask';
import {BASE_PATH} from './swagger/variables';
import {OtpAuthComponent} from './otp-auth/otp-auth.component';
import {ToNumberPipe} from './_pipes/to-number.pipe';
import {CollectionComponent, MyCollection} from './report_components/collection/collection.component';
import {fakeBackendProvider} from './_helpers';
import {MockBackend} from '@angular/http/testing';

@NgModule({
  declarations: [AppComponent,
    HeaderComponent,
    KeyboardComponent,
    HelpComponent,
    NavMenuComponent,
    NewsComponent,
    CategoriesComponent,
    FastAccessComponent,
    ProductListComponent,
    CenterComponent,
    CheckBeginComponent,
    CheckProcessComponent,
    InputFormComponent,
    TranslatePipe,
    HomeComponent,
    LoginComponent,
    AlertComponent,
    SafeHtmlPipe,
    MyCurrencyPipe,
    ReportsComponent,
    OperationReportComponent,
    ToRegexPipe,
    MyDialog,
    MyCollection,
    GlobalDialog,
    ChangePassComponent,
    AuthHistoryComponent,
    StatusComponent,
    OtpAuthComponent,
    ToNumberPipe,
    CollectionComponent],
  imports: [CurrencyMaskModule,
    BrowserModule,
    FormsModule,
    HttpModule,
    routing,
    ReactiveFormsModule,
    NgPipesModule, // materialize
    NoopAnimationsModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatInputModule,
    MatSelectModule,
    MatDialogModule,
    MatTableModule,
    TextMaskModule
  ],
  exports: [InputFormComponent],
  providers: [TRANSLATION_PROVIDERS,
    GlobalService,
    AuthGuard,
    AlertService,
    AuthenticationService,
    UserService,
    CategoryService,
    NewsService,
    ProductService,
    OperationService,
    ReportsService, // providers for Swagger services
    Configuration,
    AuthApi,
    UserApi,
    ArticleApi,
    ReportApi,
    OperationApi,
    // providers used to create fake backend
    fakeBackendProvider,
    MockBackend,
    BaseRequestOptions,
    {
      provide: APP_BASE_HREF,
      useValue: '/'
    },
    // {provide: LOCALE_ID, useValue: 'ru-RU'}
    /** base path for transport services (SWAGGER)*/
    {provide: BASE_PATH, useValue: '/api/v0'},
    {provide: BASE_PATH, useValue: 'https://m.globalpay.com.ua/api/v0'},
  ],
  entryComponents: [
    MyDialog,
    MyCollection,
    GlobalDialog],
  bootstrap: [AppComponent]
})

export class AppModule {
}



