export class CollectionExtended {
  id: number;
  tradePoint: string;
  pointAddr: string;
  date: Date;
  nominal: number;
  commission: number;
  quantity: number;
  summary: number
  incomeTariff: number;
  collectorGet: number;
  saleRecord: {
    article: string,
    cashier: string;
    quantity: number,
    nominal: number,
    commission: number,
    incomeTariff: number,
    firstOperation: Date,
    lastOperation: Date
  }[];


  constructor(id: number, tradePoint: string, pointAddr: string, date: Date, nominal: number, commission: number, quantity: number, summary: number, incomeTariff: number, collectorGet: number, saleRecord: { article: string; cashier: string; quantity: number; nominal: number; commission: number; incomeTariff: number; firstOperation: Date; lastOperation: Date }[]) {
    this.id = id;
    this.tradePoint = tradePoint;
    this.pointAddr = pointAddr;
    this.date = date;
    this.nominal = nominal;
    this.commission = commission;
    this.quantity = quantity;
    this.summary = summary;
    this.incomeTariff = incomeTariff;
    this.collectorGet = collectorGet;
    this.saleRecord = saleRecord;
  }

  static toCollectionExt(data) {
    // console.error(data);
    let saleRecors = [];
    if (data) {
      for (let i = 0; i < data.saleRecord.length; i++) {
        saleRecors.push({
          article: data.saleRecord[i].article,
          cashier: data.saleRecord[i].cashier,
          quantity: data.saleRecord[i].quantity,
          nominal: data.saleRecord[i].nominal / 100,
          commission: data.saleRecord[i].commission / 100,
          incomeTariff: data.saleRecord[i].incomeTariff / 100,
          firstOperation: data.saleRecord[i].firstOperation,
          lastOperation: data.saleRecord[i].lastOperation
        })
      }
      let collection = new CollectionExtended(
        data.id,
        data.tradePoint,
        data.tradeAdress,
        data.date,
        data.nominal / 100,
        data.commission / 100,
        data.quantity,
        data.sum / 100,
        data.incomeTariff / 100,
        data.collectorGet / 100,
        saleRecors);
      return collection
    }
    else
      return null
  }
}



