import {EventEmitter, Injectable} from '@angular/core';

@Injectable()
export class GlobalService {

  /** Flag for hiding debug messages from console.log() and console.warn() methods */
  public static hideConsole = true;

  /** Flag and path of page for redirection to market and return there after logout*/
  public static redirectToMain = false;
  public static redirectPath = "https://" + location.hostname.substr(location.hostname.indexOf(".") + 1);

  /** Flag for blocking all main actions in project.
   * Used only in operation creation to block all actions while operation in progress
   * default value - false */
  public static blockedNavigation = false;

  /** Applications href*/
  public static playstore = "https://play.google.com/store/apps/details?id=ua.com.globalpay.globalpay";
  public static appstore = "#";


  activity = 0;
  currOnline: EventEmitter<any> = new EventEmitter();
  static showList: EventEmitter<any> = new EventEmitter();
  public static navMenu = false;
  online;

  /**AUDIO FILES*/
  /**main audio chanel*/
  public static audio = new Audio();

  /**preloaded audio objects*/
  public static tink = new Audio('assets/audio/tink.mp3');
  public static failed = new Audio('assets/audio/failure.mp3');
  public static payed = new Audio('assets/audio/payed.mp3');
  /**-----------------------------------------------------*/
  currActivity: EventEmitter<number> = new EventEmitter();
  public static currDialog: EventEmitter<any> = new EventEmitter();


  constructor() {
    this.checkOnline();
  }

  static showMenu() {
    this.navMenu = true;
    const body = document.getElementsByTagName('body');
    body[0].classList.toggle('show-menu');
    if (document.getElementById('lang-help'))
      document.getElementById('lang-help').style.display = 'none';
    this.showList.emit(false);
  }

  static closeMenu() {
    this.navMenu = false;
    const body = document.getElementsByTagName('body');
    body[0].classList.remove('show-menu');
  }

  static checkMobileView() {
    return screen.availWidth < 770;
  }

  /**
   * Method for setting current activity by it`s Id:
   * 0 - market
   * REPORTS
   * 1 - sales
   * 2 - opHistory
   * 3 - collection
   * 4 - detailed sales
   * 5 - financial
   * 6 - shift
   * OPTIONS
   * (9) - my contacts (will be done)
   * 7 - authHistory
   * 8 - changePassword
   * @param act - activity Id
   */
  setActivity(act) {
    GlobalService.closeMenu();
    this.activity = act;
    this.currActivity.emit(this.activity);
  }

  checkOnline() {
    this.online = navigator.onLine;
    // alert(this.online);
    if (!this.online) {
      this.currOnline.emit({
        error: 'Нет интернета',
        errorText: 'Проверьте соединение интернет и обновите страницу'
      });
      return false;
    } else {
      return true;
    }
  }

  /**
   obj = {
      title: string, - name of dialog
      body: string, - html template to show as body
      closable: boolean - flag is dialog able to be closed or not
   }*/
  public static showDialog(obj?) {
    if (obj) {
      this.currDialog.next(obj);
    }
    else {
      this.currDialog.next(null);
    }
  }
}



