import {EventEmitter, Injectable} from "@angular/core";
import {ReportApi} from "../swagger/api/ReportApi";
import {AlertService} from "./alert.service";
import {OperationApi} from "../swagger/api/OperationApi";
import {Operation} from "../_models/operation";
import {UserApi} from "../swagger/api/UserApi";

@Injectable()
export class ReportsService {


  currReportContent: EventEmitter<string> = new EventEmitter();
  reportContent = '';

  constructor(private service: ReportApi, private alert: AlertService, private operation: OperationApi, private user: UserApi) {
  }

  static toOpList(data) {
    const array = [];
    for (let i = 0; i < data.length; i++) {
      array.push(
        new Operation(data[i].id, data[i].article, data[i].type, new Date(data[i].timestamp).toISOString(), data[i].fields, data[i].amount,
          data[i].commission, data[i].comment, data[i].status, data[i].cheque, data[i].icon));
    }
    return array;
  }

  setContent(content) {
    this.reportContent = '';
    this.reportContent = content;
    this.currReportContent.emit(content);
  }

  getCollection() {
    const cult = localStorage.lang;
    return this.service.reportShiftGet(cult);
  }

  createCollection() {
    // this.clearReport();
    console.log('GET COLLECTION');
    const cult = localStorage.lang;
    return this.service.reportCollectionPost(new Date(), cult);
  }

  getCollectionHistory(sDate, eDate) {
    console.log('GET COLLECTION');
    const cult = localStorage.lang;
    return this.service.reportCollectionHistoryGet(sDate, eDate, cult);
  }

  getCollectionById(id) {
    return this.service.reportCollectionOpIdGet(id);
  }

  getShift() {
    // this.clearReport();
    console.log('GET SHIFT');
    const cult = localStorage.lang;
    this.service.reportShiftGet(cult).subscribe(data => {
      console.log('DUTY_REPORT');
      console.log(data);
      this.setContent(data.body);
    }, error => {
      this.setContent('ERROR! Operation temporary unavailable!');
      this.alert.onError(error);
    });
  }

  getOperationHistory(count: number, dateFrom: Date, dateTill: Date, startFrom: number, desc: string) {
    // this.clearReport();
    return this.operation.operationHistoryGet(count, dateFrom, dateTill, startFrom, desc);
  }

  getSales(dateFrom: Date, dateTill: Date) {
    // this.clearReport();
    console.log('GET SALES');
    const cult = localStorage.lang;
    console.warn('LANG - ' + cult);
    this.service.reportSalesGet(dateFrom, dateTill, cult).subscribe(data => {
      console.log('SALES_REPORT');
      console.log(data);
      this.setContent(data.body);
    }, error => {
      this.setContent('ERROR! Operation temporary unavailable!');
      this.alert.onError(error);
    });
  }

  getSalesDetailed(dateFrom: Date, dateTill: Date) {
    // this.clearReport();
    console.log('GET SALES DETAILED');
    const cult = localStorage.lang;
    console.warn('LANG - ' + cult);
    this.service.reportSalesDetailedGet(dateFrom, dateTill, cult).subscribe(data => {
      console.log('DETAILED_REPORT');
      console.log(data);
      this.setContent(data.body);
    }, error => {
      this.setContent('ERROR! Operation temporary unavailable!');
      this.alert.onError(error);
    });
  }

  getFinancial(date: Date) {
    // this.clearReport();
    console.log('GET FINANCIAL');
    const cult = localStorage.lang;
    console.warn('LANG - ' + cult);
    this.service.reportFinancialGet(date, cult).subscribe(data => {
      console.log('FINANCIAL_REPORT');
      console.log(data);
      this.setContent(data.body);
    }, error => {
      this.setContent('ERROR! Operation temporary unavailable!');
      this.alert.onError(error);
    });
  }

  getAuthHistory() {
    console.log('GET AUTH HISTORY');
    return this.user.userAuthHistoryGet();
  }

  static printReport(report) {

    let sDate = '', eDate = '';

    if (report.sDate != null)
      sDate = '' + report.sDate.getDate() + '.' +
        ('0' + (report.sDate.getMonth() + 1)).slice(-2) + '.' + report.sDate.getFullYear();

    if (report.eDate != null)
      eDate = '' + report.eDate.getDate() + '.' +
        ('0' + (report.eDate.getMonth() + 1)).slice(-2) + '.' + report.eDate.getFullYear();
    // + d.getHours() + ':' +
    // d.getMinutes() + ':' +
    // d.getSeconds();

    let range = (sDate != '' && eDate != '') ? ('<h5 style="text-align: center">' + sDate + ' - ' + eDate + '</h5>') : '';

    let text = '<html><head><title>' + report.name + '</title></head><body >' +
      '<h4 style="text-align: center">' + report.name + '</h4>' +
      range +
      '<div style="text-align: center; width: 400px; margin: 0 auto">' + report.content + '</div>' +
      '</body></html>';

    const printWindow = window.open('', '', 'height=800,width=800');
    printWindow.document.write(text);

    printWindow.document.close(); // necessary for IE >= 10
    printWindow.focus(); // necessary for IE >= 10*/

    printWindow.print();
    printWindow.close();

    return true;
  }

}



