import { Field } from './field';
import { Commission } from './commission';

export class Product {
  pId: number;
  pType: number;
  pName: { cult: string, value: string }[];
  pHint: Array<{ cult: '', value: '' }>;
  pInputFields: Field[];
  pCommissions: Commission[];
  pNominal: number;
  pCheque: string;
  pProvCodes: string[];
  pAmountMin: number;
  pAmountMax: number;
  pIcon: string;
  pOrder: number;


  constructor(pId: number, pType: number, pName: { cult: string, value: string }[], pHint: Array<{ cult: '', value: '' }>,
    pInputFields: Field[], pCommissions: Commission[], pNominal: number, pCheque: string, pProvCodes: string[], pAmountMin: number,
    pAmountMax: number, pIcon: string, pOrder: number) {
    this.pId = pId;
    this.pType = pType;
    this.pName = pName;
    this.pHint = pHint;
    this.pInputFields = pInputFields;
    this.pCommissions = pCommissions;
    this.pNominal = pNominal;
    this.pCheque = pCheque;
    this.pProvCodes = pProvCodes;
    this.pAmountMin = pAmountMin;
    this.pAmountMax = pAmountMax;
    this.pIcon = pIcon;
    this.pOrder = pOrder;
  }

}



