import {Component, Input, OnInit} from "@angular/core";
import {Lang} from "../../translate/Lang";
import {OperationService} from "../../_services/operation.service";
import {AlertService} from "../../_services/alert.service";
import {ProductService} from "../../_services/product.service";
import {Operation} from "../../_models/operation";
import {GlobalService} from "../../_services/global.service";

@Component({
  selector: 'app-check-process',
  templateUrl: './check-process.component.html',
  styleUrls: ['./check-process.component.css']
})
export class CheckProcessComponent
  implements OnInit {

  @Input() opResult;
  statusComplete = 2;
  transId = 0;
  onBalance = 0;
  inAmount = 0;
  sumCommission;
  loading = false;
  time;
  sbTime;

  constructor(private service: OperationService, private prodServ: ProductService, private alert: AlertService) {
    this.service.finalStatus.subscribe((value) => {
      this.time = new Date();
      if (value == 1) {
        this.service.redirect = true;
        // console.error("TIMER " + this.service.OPERATION.opId + " START");
        this.sbTime = setTimeout(() => {
          // console.error("TIMER " + this.service.OPERATION.opId + " ENDS");
          if (this.service.redirect) {
            // console.error("TIMER " + this.service.OPERATION.opId + " redirect for" + this.sbTime);
            this.service.clearOperation();
          }
        }, 15000);
      }
      this.statusComplete = value;
      this.playSound();
    });
    this.service.currTransId.subscribe((value) => {
      this.transId = value;
    });
  }

  ngOnDestroy() {
    this.service.redirect = false;
    // console.error("DESTROY Check-process");
    // console.error(this.service.redirect);
    clearTimeout(this.sbTime);
  }

  ngOnInit() {
    this.time = null;
    this.statusComplete = 2;
    this.opResult = this.service.OPERATION;
    this.calcValues(this.opResult);
    console.warn(GlobalService.failed);
    console.warn(GlobalService.payed);
    this.playSound();
  }


  public setL(names) {
    return Lang.getLangValue(names);
  }


  calcValues(operation) {
    this.sumCommission = operation.commission;
    console.warn(this.sumCommission);
    console.log(operation.commission);
    if (this.sumCommission > 0) {
      this.onBalance = this.opResult.amount / 100;
      this.sumCommission = Math.abs(this.sumCommission);
      this.inAmount = this.opResult.amount / 100 + this.sumCommission;

    }
    if (this.sumCommission < 0) {
      this.onBalance = Math.round((this.opResult.amount / 100 + this.sumCommission) * 100) / 100;
      this.sumCommission = Math.abs(this.sumCommission);
      this.inAmount = this.opResult.amount / 100;

    }
    if (this.sumCommission === 0) {
      this.onBalance = this.opResult.amount / 100;
      this.inAmount = this.opResult.amount / 100;
      this.sumCommission = Math.abs(this.sumCommission);
    }
  }

  getOpStatus(transId) {
    if (this.opResult.product.pType === 1) {
      this.loading = true;
      this.service.voucherGetStatus(transId).subscribe(data => {
        console.log(data);
        this.opResult.status = data;
        this.loading = false;
        this.time = new Date();
      }, error => {
        this.loading = false;
        this.alert.onError(error);
      });
    }
    if (this.opResult.product.pType === 2) {
      // alert('TEMPORARY UNAVAILABLE');
      this.loading = true;
      this.service.replGetStatus(transId).subscribe(
        data => {
          console.log(data);
          this.opResult.status = data;
          this.time = new Date();
          this.loading = false;
        },
        error => {
          this.loading = false;
          this.alert.onError(error);
        }
      );
    }
  }

  playSound() {
    // if (this.statusComplete == 1) {
    //   GlobalService.audio.src = 'assets/audio/payed.mp3';
    //   // GlobalService.audio.load();
    //   GlobalService.audio.play();
    // }
    // if (this.statusComplete == 3) {
    //   GlobalService.audio.src = 'assets/audio/failure.mp3';
    //   // GlobalService.audio.load();
    //   GlobalService.audio.play();
    // }

    if (this.statusComplete == 1) {
      GlobalService.audio = GlobalService.payed;
      GlobalService.audio.play();
    }
    if (this.statusComplete == 3) {
      GlobalService.audio = GlobalService.failed;
      GlobalService.audio.play();
    }
  }

  printC() {
    console.log('PRINT CHEQUE');
    const operation = new Operation(this.service.OPERATION.opId, this.service.OPERATION.product.pName, this.service.OPERATION.product.pType,
      this.time.toString(), this.service.OPERATION.fieldValues, // [],
      this.service.OPERATION.amount, this.service.OPERATION.commission * 100, this.service.OPERATION.comment, this.service.OPERATION.status,
      this.service.OPERATION.product.pCheque, '');
    if (operation.oType === 2) {
      console.log('PRINT REPLENISHMENT');
      const cheque = this.prodServ.ChequeText('gen.prn');
      this.prodServ.printCheque(operation, cheque);
    }
    if (operation.oType === 1 && operation.oStatus.secret) {
      console.log('PRINT VOUCHER');
      const cheque = this.prodServ.ChequeText('lifeex.prn');
      this.prodServ.printCheque(operation, cheque);
    }
  }

}



