import {ChangeDetectorRef, Component, Inject, OnInit} from '@angular/core';

import {User} from '../_models/user';
import {Configuration} from '../swagger/configuration';
import {AuthenticationService} from '../_services/authentication.service';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material';
import {Lang} from '../translate/Lang';
import {GlobalService} from '../_services/global.service';
import {AlertService} from '../_services/alert.service';
import {ProductService} from '../_services/product.service';
import {UserService} from '../_services/user.service';
import {NewsService} from "../_services";

@Component({
  moduleId: module.id,
  templateUrl: 'home.component.html',
  styleUrls: ['home.component.css']
})

export class HomeComponent
  implements OnInit {

  currentUser: User;

  constructor(private configuration: Configuration, private global: GlobalService, private alert: AlertService,
              private auth: AuthenticationService, private user: UserService, public dialog: MatDialog, private prodServ: ProductService,
              private cdr: ChangeDetectorRef) {
    this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
    this.user.USER = this.currentUser;
    if (navigator.onLine) {
      console.log('CLIENT ONLINE');
    } else {
      console.log('CLIENT OFF');
      console.error('client offline');
    }
    this.global.currOnline.subscribe((val) => {
      if (val) {
        this.openDialog(val.error, val.errorText);
      }
    });
    GlobalService.currDialog.subscribe(
      (data) => {
        if (data != null) {
          this.openDialog(data.title, data.body, data.closeable)
        }
      }
    );
    // this.alert.currError.subscribe((val) => {
    //   if (val) {
    //     this.openDialog(val.error, val.errorText);
    //   }
    // });
  }


  ngOnInit() {
    this.global.checkOnline();
    // this.service.watchGeo();
    if (localStorage.getItem('currentUser')) {
      this.user.USER = JSON.parse(localStorage.getItem('currentUser'));
      console.log(JSON.parse(localStorage.getItem('currentUser')));
      this.configuration.apiKey = JSON.parse(localStorage.getItem('currentUser')).keyToken;
    } else {
      // if (this.configuration.apiKey === "") {
      this.auth.logout();
      // }
    }
    this.prodServ.getChequesList();
    // this.loadAllUsers();
  }

  ngAfterViewInit() {
    this.cdr.detectChanges();
  }

  openDialog(title, message, closeable?) {
    let config = {};
    if (window.screen.availWidth < 720) {
      config = {
        disableClose: true,
        maxHeight: '80vh',
        width: '92vw',
        minWidth: '300px',
        marginLeft: '2vw'
      };
    } else {
      config = {
        disableClose: true,
        maxHeight: '550px',
        width: '300px',
      };
    }
    const dialogRef = this.dialog.open(GlobalDialog, config);
    dialogRef.componentInstance.title = title;
    dialogRef.componentInstance.message = message;
    if (closeable)
      dialogRef.componentInstance.closeable = closeable;


    // console.log('dialogRef', dialogRef);
  }

}


@Component({
  selector: 'globalDialog',
  templateUrl: 'dialog.html',
})
export class GlobalDialog {

  title = 'TEST';
  message = 'test text';
  closeable = false;

  constructor(public dialogRef: MatDialogRef<GlobalDialog>,
              private cdr: ChangeDetectorRef,
              @Inject(MAT_DIALOG_DATA) public data: any) {
    window.scrollTo(0, 1);
  }

  ngOnInit() {
  }

  public setL(names) {
    return Lang.getLangValue(names);
  }

  closeDialog() {
    if (NewsService.showApps)
      NewsService.showApps = false;
  }
}



