import {Component, OnInit, ViewChild} from '@angular/core';
import {AuthenticationService} from '../_services/authentication.service';

@Component({
  selector   : 'app-change-pass',
  templateUrl: './change-pass.component.html',
  styleUrls  : ['./change-pass.component.css']
})
export class ChangePassComponent
  implements OnInit {

  old_pass: string;
  new_pass: string;
  new_pass_repeat: string;
  loading = false;
  showPassword = false;
  success = false;
  error = false;
  @ViewChild('f') passForm: HTMLFormElement;

  constructor(private auth: AuthenticationService) {
  }

  ngOnInit() {
  }

  changePass() {
    if (this.old_pass !== '' && this.new_pass !== '' && this.new_pass_repeat !== '') {
      if (this.new_pass === this.new_pass_repeat) {
        const passwords = {
          oldPassword: this.old_pass,
          newPassword: this.new_pass
        };
        this.loading = true;
        console.log(passwords);
        this.auth.changePass(passwords).subscribe(data => {
          console.log(data);
          this.loading = false;
          this.passForm.reset();
          this.success = true;
          setTimeout(() => {
            this.success = false;
          }, 15000);
        }, () => {
          this.error = true;
          this.loading = false;
          this.passForm.reset();
          setTimeout(() => {
            this.error = false;
          }, 15000);
        });
      }
    }
  }
}



