export class Operation {
  oId: string;
  oArticle: { cult: string, value: string }[];
  oType: number;
  oTimestamp: string;
  oFields: { id: number, value: string }[];
  oAmount: number;
  oCommission: number;
  oComment: string;
  oStatus: any;
  oCheque: string;
  oIcon: string;


  // --------------------
  constructor(oId: string, oArticle: { cult: string, value: string }[], oType: number, oTimestamp: string,
    oFields: { id: number; value: string }[], oAmount: number, oCommission: number, oComment: string, oStatus: any, oCheque: string,
    oIcon: string,) {
    this.oId = oId;
    this.oArticle = oArticle;
    this.oType = oType;
    this.oTimestamp = oTimestamp;
    this.oFields = oFields;
    this.oAmount = oAmount;
    this.oCommission = oCommission;
    this.oComment = oComment;
    this.oStatus = oStatus;
    this.oCheque = oCheque;
    this.oIcon = oIcon;

  }

}



