export class Lang {

  public static def_lang = 'ru'; // if def_lang is uk-UA change 'ru' to 'uk'

  public static getLangValue(names) {
    let currLang = '';
    let value = '';
    let ru,
      uk,
      en;
    if (localStorage.lang) {
      currLang = localStorage.lang;
    } else {
      currLang = this.def_lang;
    }

    for (let i = 0; i < names.length; i++) {
      if (names[i].cult === currLang && names[i].value !== '') {
        value = names[i].value;
        return value;
      } else {
        if (names[i].cult === 'ru') {
          ru = names[i].value;
        }
        if (names[i].cult === 'uk') {
          uk = names[i].value;
        }
        if (names[i].cult === 'en') {
          en = names[i].value;
        }
      }
    }

    // !value->UK->RU->EN
    if (value === '') {
      if (uk !== '') {
        return uk;
      } else {
        if (ru !== '') {
          return ru;
        } else {
          if (en !== '') {
            return en;
          } else {
            return '';
          }
        }
      }
    }
  }
}



