import {EventEmitter, Injectable} from '@angular/core';
import {NavigationStart, Router} from '@angular/router';
import {Observable} from 'rxjs';
import {Subject} from 'rxjs/Subject';
import {AuthenticationService} from './authentication.service';
import {MatDialog} from '@angular/material';
import {Lang} from '../translate/Lang';
import {GlobalService} from "./global.service";

@Injectable()
export class AlertService {
  currError: EventEmitter<any> = new EventEmitter();
  private subject = new Subject<any>();
  private keepAfterNavigationChange = false;

  constructor(private router: Router, private auth: AuthenticationService, public dialog: MatDialog) {
    // clear alert message on route change
    router.events.subscribe(event => {
      if (event instanceof NavigationStart) {
        if (this.keepAfterNavigationChange) {
          // only keep for a single location change
          this.keepAfterNavigationChange = false;
        } else {
          // clear alert
          this.subject.next();
        }
      }
    });
  }


  success(message: string, keepAfterNavigationChange = false) {
    this.keepAfterNavigationChange = keepAfterNavigationChange;
    this.subject.next({
      type: 'success',
      text: message
    });
  }

  error(message: string, keepAfterNavigationChange = false) {
    this.keepAfterNavigationChange = keepAfterNavigationChange;
    this.subject.next({
      type: 'error',
      text: message
    });
  }

  getMessage(): Observable<any> {
    return this.subject.asObservable();
  }

  // showError(error) {
  //   this.currError.emit(error);
  // }


  onError(error) {
    // console.log("ERROR");
    // console.log(error);
    switch (error.status) {
      case 500: {
        break;
      }
      case 501: {

        break;
      }
      case 502: {
        console.log('Server temporary unavailable');
        GlobalService.showDialog({
          title: 'Сервер не отвечает',
          body: 'Сервер недоступен. Попробуйте зайти на сайт позже.'
        });
        break;
      }
      case 401: {
        console.log('ERROR');
        console.log(error);
        console.log('ERROR_STATUS = ' + error.status);
        // console.log("ERROR_BODY = " + error._body);
        console.log('LOGOUT');
        this.auth.logout();
        break;
      }
      case 403: {
        const err = JSON.parse(error._body);
        // alert("ERROR: " + err);
        console.error('ERROR: ' + err.error + ' - ' + Lang.getLangValue(err.errorText));

      }
      default: {
        if (error.status === 0) {
          GlobalService.showDialog({
            title: 'Нет интернета',
            body: 'Проверьте соединение интернет и обновите страницу'
          });
        } else {
          console.log('ERROR');
          console.log(error);
          // alert(error);
        }
        // console.log("ERROR_STATUS = " + error.status);
        // console.log("ERROR_BODY = " + error._body);
      }
    }
  }

}




