import {Component, OnInit} from '@angular/core';
import {Lang} from '../translate/Lang';
import {CategoryService} from '../_services/category.service';
import {ProductService} from '../_services/product.service';
import {AlertService} from '../_services/alert.service';
import {GlobalService} from '../_services/global.service';
import {NewsService} from "../_services/news.service";

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent
  implements OnInit {

  public categories = this.catService.CATEGORIES;
  currActivity = 0;
  showNews;
  currCatId;
  mobileView;

  constructor(private catService: CategoryService,
              private prodService: ProductService,
              private newsServ: NewsService,
              private global: GlobalService,
              private alert: AlertService) {

    this.mobileView = window.innerWidth <= 1115;
    this.catService.currCat.subscribe((value) => {
      this.currCatId = value;
    });
    this.global.currActivity.subscribe((value) => {
      this.currActivity = value;
    });
    this.newsServ.currNews.subscribe((value) => {
      this.showNews = value;
    });
    // if (this.categories.length > 0) {
    //   if (this.catService.CurrentCATEGORY.cId === 0) {
    //     this.catService.setCategory(this.categories.filter(function (obj) {
    //       return obj.cOrder == 1;
    //     }))
    //   }
    // }

  }

  ngOnInit() {
    this.catService.getCategories().subscribe(data => {
      this.categories = [];
      this.categories = this.catService.getCategoriesList(data);
      console.log('CATEGORIES');
      console.log(this.categories);
      for (let i = 0; i < this.categories.length; i++) {
        this.prodService.ProdsByCat.push({
          category: this.categories[i].cId,
          products: []
        });
      }
      localStorage.setItem('ProductsByCategory', JSON.stringify(this.prodService.ProdsByCat));
      if (this.categories.length > 0) {
        console.warn('categories>0');
        console.warn(this.catService.CurrentCATEGORY);
        if (this.catService.CurrentCATEGORY.cId === 0) {
          console.warn(this.catService.CurrentCATEGORY.cId);
          this.catService.setCategoryByOrderId(1);
        } else {
          console.warn(this.catService.CurrentCATEGORY.cId);
          this.catService.setCategory(this.catService.CurrentCATEGORY);
        }
      }
    }, error => {
      // console.log("STATUS = " + error.status);
      this.alert.onError(error);
    });
  }

  Lang(names) {
    return Lang.getLangValue(names);
  }


  selectCategory(category) {
    if (!GlobalService.blockedNavigation) {
      // console.error(category);
      this.global.checkOnline();
      if (this.currActivity !== 0) {
        this.global.setActivity(0);
      }
      this.catService.setCategory(category);
      this.prodService.clearCurrent();
    }
  }
}



