import {Component, Inject, OnInit} from "@angular/core";
import {GlobalService} from "../../_services/global.service";
import {ReportsService} from "../../_services/reports.service";
import {Lang} from "../../translate/Lang";
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from "@angular/material";
import {CollectionExtended} from "../../_models/collection-extended";
import {Collection} from "../../_models/collection";
import {AlertService} from "../../_services";

@Component({
  selector: 'app-collection',
  templateUrl: './collection.component.html',
  styleUrls: ['./collection.component.css']
})
export class CollectionComponent implements OnInit {
  mobileView;
  /**
   * Activities
   * 1 - sales
   * 2 - opHistory
   * 3 - collection
   * 4 - detailed sales
   * 5 - financial
   * 6 - shift
   * OPTIONS
   * (9) - my contacts (in future)
   * 7 - authHistory
   * 8 - changePassword
   * */
  currActivity = this.global.activity;
  reportContent = '';
  history = '';
  loading1 = false;
  loading2 = false;
  collectionList = [];
  actName = 'create_collection';

  reportCreated = false;
  success_collect = false;
  sDate: Date = new Date;
  eDate: Date = new Date;
  showPrint = false;

  constructor(private global: GlobalService,
              private report: ReportsService,
              private alert: AlertService,
              public dialog: MatDialog) {
    this.reportCreated = false;
    if (window.innerWidth < 720) {
      // console.error('changed');
      this.mobileView = true;
    }
  }

  ngOnInit() {
    this.mobileView = GlobalService.checkMobileView();
    // console.error(this.mobileView);
    // this.collectionList = [
    //   new Collection(1223235, 1, "TradePoint", new Date(), 20, 5, 5, 20, 25, 25, new Date(), new Date()),
    //   new Collection(1346343, 2, "TradePoint", new Date(), 20, 5, 5, 20, 25, 25, new Date(), new Date()),
    //   new Collection(1454544, 3, "TradePoint", new Date(), 20, 0, 5, 20, 25, 25, new Date(), new Date()),
    //   new Collection(1543543, 4, "TradePoint", new Date(), 20, 5, 5, 20, 25, 25, new Date(), new Date()),
    // ];


    this.sDate.setHours(0);
    this.sDate.setMinutes(0);
    this.sDate.setSeconds(0);
    this.sDate.setMilliseconds(0);

    this.eDate.setHours(23);
    this.eDate.setMinutes(59);
    this.eDate.setSeconds(59);
    this.eDate.setMilliseconds(999);


    this.clearContent();

    this.reportContent = '';
    this.history = '';
    // this.report.currReportContent.subscribe((val) => {
    //   this.reportContent = val;
    //   this.loading1 = false;
    // });
    console.log('CURR_ACTIVITY = ' + this.currActivity);
  }


  clearContent() {
    this.showPrint = false;
    this.reportContent = '';
    this.history = '';
  }

  setLang(name) {
    return Lang.getLangValue(name);
  }

  checkDates() {
    console.warn(this.sDate);
    console.warn(this.eDate);
    if (this.sDate.getTime() > this.eDate.getTime()) {
      document.getElementById('EndDate').style.borderBottomColor = 'red';
      document.getElementById('StartDate').style.borderBottomColor = 'red';
      document.getElementById('date-error').style.display = 'block';
      return false;
    } else {
      document.getElementById('EndDate').style.borderBottomColor = '#898989';
      document.getElementById('StartDate').style.borderBottomColor = '#898989';
      document.getElementById('date-error').style.display = 'none';
      return true;
    }
  }


  printReport() {
    let report = {
      content: this.reportContent,
      sDate: null,
      eDate: null,
      name: document.getElementById('actName').innerText
    };
    ReportsService.printReport(report);
  }


  createReport() {
    this.loading1 = true;
    this.success_collect = false;
    if (this.reportCreated == true) {
      this.report.createCollection().subscribe(data => {
        this.showPrint = true;
        this.reportContent = data.body;
        this.loading1 = false;
        this.reportCreated = false;
        this.success_collect = true;
        this.actName = 'create_collection';
      }, error => {
        this.reportContent = 'Операция временно недоступна. Попробуйте повторить позже.';
        this.showPrint = false;
        this.reportCreated = false;
        // this.alert.onError(JSON.parse(error._body));
        this.loading1 = false;
        this.actName = 'create_collection';
        this.alert.onError(error);
      });
    }
    else {
      this.report.getCollection().subscribe(
        data => {
          this.reportContent = data.body;
          this.showPrint = false;
          this.loading1 = false;
          this.actName = 'execute';
          this.reportCreated = true;
        },
        error => {
          this.showPrint = false;
          this.loading1 = false;
          this.reportCreated = false;
          this.alert.onError(error);
        }
      );
    }
  }

  completeAction() {
    if (this.checkDates()) {
      console.warn('COLLECTION');
      this.report.createCollection();
      this.loading1 = true;
      setTimeout(() => {
        this.loading1 = false;
      }, 40000);
    }
  }

  getCollectionHistory() {
    this.loading2 = true;
    this.report.getCollectionHistory(this.sDate, this.eDate).subscribe(
      data => {
        console.log('COLLECTION_REPORT');
        // console.error(data);
        this.collectionList = Collection.toCollectionArray(data);
        // console.error(this.collectionList);
        this.loading2 = false;
      },
      error => {
        this.collectionList = [];
        this.loading2 = false;
        this.alert.onError(error);
      }
    );
  }

  openDialog(collectionId) {

    // console.error(collectionId);
    this.report.getCollectionById(collectionId).subscribe(
      data => {
        // console.error(data);
        let config = {};
        if (window.innerWidth < 721) {
          config = {
            height: '85%',
            width: '96vw'
          };
        } else {
          config = {
            height: '700px',
            width: '780px'
          };
        }
        const dialogRef = this.dialog.open(MyCollection, config);
        dialogRef.componentInstance.Collection = CollectionExtended.toCollectionExt(data);
        // collection = data;
      },
      error => {
        // console.error(error);
      }
    )
    // collection = new Collection(12, "TradePoint", new Date(), []);

  }
}

@Component({
  selector: 'collectDialog',
  styleUrls: ['./collection.component.css'],
  templateUrl: 'dialog.html',
})
export class MyCollection {

  Collection: CollectionExtended;

  mobileView = GlobalService.checkMobileView();
  addScroll = '';

  constructor(public dialogRef: MatDialogRef<MyCollection>,
              private report: ReportsService,
              @Inject(MAT_DIALOG_DATA) public data: any) {
    if (this.mobileView) {
      this.addScroll = "scroll"
    } else {
      this.addScroll = "normal"
    }
  }

  ngOnInit() {
    // console.error(this.Collection)
  }


  public setL(names) {
    return Lang.getLangValue(names);
  }

}



