export class Balance {
  bPersonal: number;
  bNetwork: number;
  bOverdraft: number;
  bDateTime: Date;

  constructor(bPersonal: number, bNetwork: number, bOverdraft: number, bDateTime: Date) {
    this.bPersonal = bPersonal;
    this.bNetwork = bNetwork;
    this.bOverdraft = bOverdraft;
    this.bDateTime = bDateTime;
  }

}



