import {Component, OnInit} from '@angular/core';
import {AlertService} from '../_services/alert.service';
import {NewsService} from '../_services/news.service';
import {News} from '../_models/news';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent
  implements OnInit {

  NEWS: News[] = [];
  haveNews = false;
  showNews = true;

  constructor(private service: NewsService,
              private alert: AlertService) {
    this.showNews = this.service.showNews;
    if (!this.showNews && this.NEWS.length != 0) {
      this.closeNews();
    }
  }

  ngOnInit() {
    // this.showNews = this.service.showNews;
    if (!this.showNews) {
      this.closeNews();
    }
    this.service.getNews().subscribe(data => {
      console.warn(data);
      this.NEWS = this.service.getNewsList(data);
      // this.NEWS = [];
      if (this.NEWS.length > 0) {
        this.haveNews = true;
        document.getElementById('main-wrap').classList.remove('no-news');
      }
      console.log('NEWS');
      console.log(this.NEWS);
    }, error => {
      // document.getElementById("main-wrap").classList.add("no-news");
      this.NEWS = [];
      // this.closeNews();
      this.alert.onError(error);
    });
  }

  closeNews() {
    document.getElementById('modal-news').classList.toggle('hide-news');
    this.service.showNews = false;
  }
}




