export class Collection {
  opId: number;
  id: number;
  tradePoint: string;
  date: Date;
  nominal: number;
  commission: number;
  sum: number;
  quantity: number;
  incomeTariff: number;
  collectorGet: number;
  firstOperation: Date;
  lastOperation: Date;


  constructor(opId: number, id: number, tradePoint: string, date: Date, nominal: number, commission: number, sum: number, quantity: number, incomeTariff: number, collectorGet: number, firstOperation: Date, lastOperation: Date) {
    this.opId = opId;
    this.id = id;
    this.tradePoint = tradePoint;
    this.date = date;
    this.nominal = nominal;
    this.commission = commission;
    this.sum = sum;
    this.quantity = quantity;
    this.incomeTariff = incomeTariff;
    this.collectorGet = collectorGet;
    this.firstOperation = firstOperation;
    this.lastOperation = lastOperation;
  }

  static toCollection(data) {
    let collection = new Collection(
      data.opId,
      data.id,
      data.tradePoint,
      data.date,
      data.nominal / 100,
      data.commission / 100,
      data.sum / 100,
      data.quantity,
      data.incomeTariff / 100,
      data.collectorGet / 100,
      data.firstOperation,
      data.lastOperation);
    return collection
  }


  static toCollectionArray(data) {
    let array = [];
    let obj: Collection;
    for (let i = 0; i < data.length; i++) {
      array.push(new Collection(
        data[i].op_id,
        data[i].id,
        data[i].tradePoint,
        data[i].date,
        data[i].nominal / 100,
        data[i].commission / 100,
        data[i].sum / 100,
        data[i].quantity,
        data[i].incomeTariff / 100,
        data[i].collectorGet / 100,
        data[i].firstOperation,
        data[i].lastOperation,
      ));
    }
    return array;
  }


}



