import {
  Component,
  OnInit
} from '@angular/core';
import { ReportsService } from '../../_services/reports.service';
import { AlertService } from '../../_services/alert.service';

@Component({
  selector   : 'app-auth-history',
  templateUrl: './auth-history.component.html',
  styleUrls  : ['./auth-history.component.css']
})
export class AuthHistoryComponent
  implements OnInit {

  authArray = [];
  loading = false;

  constructor(private report: ReportsService, private alert: AlertService) {
  }

  ngOnInit() {
    this.loading = true;
    this.report.getAuthHistory().subscribe(data => {
      console.log('AUTH HISTORY REPORT');
      console.log(data);
      this.authArray = data;
      this.loading = false;
    }, error => {
      this.alert.onError(error);
    });
  }

}



