import {Component, OnInit} from '@angular/core';
import {UserService} from "../_services/user.service";
import {ActivatedRoute, Router} from "@angular/router";
import {Configuration} from "../swagger/configuration";
import {AuthenticationService} from "../_services/authentication.service";
import {GlobalService} from "../_services/global.service";

@Component({
  selector: 'app-otp-auth',
  templateUrl: './otp-auth.component.html',
  styleUrls: ['./otp-auth.component.css']
})
export class OtpAuthComponent implements OnInit {

  token = "";
  error = false;
  private sub: any;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private configuration: Configuration,
              private authenticationService: AuthenticationService,
              private user: UserService) {
  }

  ngOnInit() {
    this.error = false;
    this.sub = this.route.params.subscribe(params => {
      this.token = params['id'] || ''; // (+) converts string 'id' to a number
      try {
        if (this.token.length > 0)
          this.loginByToken(this.token);
      }
      catch (e) {
        this.error = true;
      }
    });
  }


  loginByToken(token: string) {
    console.warn("OTP - " + token);
    this.authenticationService.loginByToken(token)
      .subscribe(
        data => {
          try {
            let user = {
              username: data.cashier,
              terminal: data.terminal,
              keyToken: data.tokens.keyToken,
              refreshToken: data.tokens.refreshToken
            };
            localStorage.setItem('currentUser', JSON.stringify(user));
            console.warn("login data saved to storage");
            this.user.USER = JSON.parse(localStorage.getItem('currentUser'));
            // set token value for requests
            this.configuration.apiKey = token;
            this.router.navigate(['/']);
            this.user.getGeo();
          } catch (error) {
            alert('Для авторизации на сайте отключите режим инкогнито');
          }
        },
        error => {
          this.error = true;
          window.location.href = GlobalService.redirectPath;
          let info;
          try {
            info = JSON.parse(error);
          }
          catch (e) {
          }
          console.warn(info);
          console.error('NO RESPONCE FROM SERVER');
        }
      );
  }

}



